<script lang="ts">
  import { onMount } from "svelte";
  import * as browser from "webextension-polyfill";
  import { isDarkMode, wallet, user, userPublicAddress, isTWA } from "~/store";
  import { createQuery, useQueryClient } from "@tanstack/svelte-query";
  import { nimbus } from "~/lib/network";
  import mixpanel from "mixpanel-browser";
  import { getLinkData, getListAddress } from "~/lib/queryAPI";
  import { detectedGeneration } from "~/lib/chains";
  import { triggerToast } from "~/utils/functions";
  import { isNimbusVersion } from "~/utils/constants";
  import { t } from "~/lib/i18n";

  import Icon from "~/UI/Option/Icon.svelte";
  import TabLeaderBoard from "~/UI/DailyCheckin/TabLeaderBoard.svelte";
  import TabQuests from "~/UI/DailyCheckin/TabQuests.svelte";
  import TabDailyCheckin from "~/UI/DailyCheckin/TabDailyCheckin.svelte";
  import TabReward from "~/UI/DailyCheckin/TabReward.svelte";
  import TabFlipGmPoints from "~/UI/DailyCheckin/TabFlipGMPoints.svelte";
  import ErrorBoundary from "~/components/ErrorBoundary.svelte";
  import ConnectX from "~/components/SUI Campaign/ConnectX.svelte";
  import Button from "~/components/Button.svelte";
  import LoadingPremium from "~/components/LoadingPremium.svelte";
  import Loading from "~/components/Loading.svelte";

  import Diamond from "~/assets/Diamond.png";
  import wheelIcon from "~/assets/wheel-icon.svg";

  export let currentRoute;

  const listSideBar = [
    {
      label: "Quests",
      value: "quests",
      type: "Quests",
    },
    {
      label: "Daily Checkin",
      value: "checkin",
      type: "Daily Checkin",
    },
    // {
    //   label: "Flip GM Points",
    //   value: "flip",
    //   type: "Flip GM Points",
    // },
    {
      label: "Leaderboard",
      value: "leaderboard",
      type: "Leaderboard",
    },
    {
      label: "Rewards",
      value: "rewards",
      type: "Rewards",
    },
  ];

  const queryClient = useQueryClient();

  let activeTabValue = "checkin";
  let socialData: any = [];
  let isSkipToMainPage = false;
  let code = "";
  let isLoadingSubmitInviteCode = false;

  let partnerQuestId = "";

  let dataReferrals: any = {};

  $: {
    browser.storage.onChanged.addListener((changes) => {
      if (changes?.options?.newValue?.lang) {
        window.location.reload();
      }
    });
  }

  let listAddress: any = [];

  $: query = createQuery({
    queryKey: ["list-address"],
    queryFn: () => getListAddress(),
    retry: false,
    enabled: $user && Object.keys($user)?.length !== 0,
  });

  $: {
    if (
      !$query.isError &&
      $query.data !== undefined &&
      $query.data?.length !== 0
    ) {
      formatDataListAddress($query.data);
    }
  }

  const formatDataListAddress = async (data: any) => {
    listAddress = data?.map((item: any) => {
      return {
        id: item.id,
        type: item?.type,
        label: item.label,
        value: item?.type === "CEX" ? item.id : item.accountId,
        logo: item?.type === "CEX" ? item.logo : detectedGeneration(item?.type),
        accounts:
          item?.accounts?.map((account: any) => {
            return {
              id: account?.id,
              type: account?.type,
              label: account?.label,
              value: account?.type === "CEX" ? account?.id : account?.accountId,
              logo:
                account?.type === "CEX"
                  ? account?.logo
                  : detectedGeneration(account?.type),
            };
          }) || [],
      };
    });
  };

  const handleSetWallet = async () => {
    const selectedWalletRes = await browser.storage.sync.get("selectedWallet");
    if (
      selectedWalletRes?.selectedWallet !== null &&
      $user &&
      Object.keys($user)?.length !== 0
    ) {
      wallet.update((n) => (n = selectedWalletRes.selectedWallet));
    } else {
      if (listAddress && listAddress?.length !== 0) {
        wallet.update(
          (n) =>
            (n =
              listAddress?.find((item: any) => item.label === "Your wallets")
                ?.value || listAddress[0]?.value),
        );
      }
    }
  };

  const getCampaignReferrals = async () => {
    const response: any = await nimbus.get("/v2/campaign/sui-unlock/referral");
    return response.data;
  };

  $: queryCampaignReferrals = createQuery({
    queryKey: ["campaign-referrals"],
    queryFn: () => getCampaignReferrals(),
    retry: false,
    enabled:
      $user &&
      Object.keys($user)?.length !== 0 &&
      !Boolean(localStorage.getItem("isSkipInviteCodeCampaign")),
  });

  $: {
    if (
      !$queryCampaignReferrals.isError &&
      $queryCampaignReferrals.data !== undefined
    ) {
      dataReferrals = $queryCampaignReferrals?.data;
    }
  }

  onMount(() => {
    mixpanel.track("checkin_page");
    const urlParams = new URLSearchParams(window.location.search);
    const tabParams = urlParams.get("tab");
    const partnerId = urlParams.get("id");
    if (tabParams) {
      activeTabValue = tabParams;
      handleUpdateParamsPartnerSelected(tabParams, partnerId);
    } else {
      activeTabValue = $isTWA ? "checkin" : "quests";
      handleUpdateParamsPartnerSelected(activeTabValue, partnerId);
    }

    isSkipToMainPage = Boolean(
      localStorage.getItem("isSkipInviteCodeCampaign"),
    );

    handleSetWallet();
  });

  const handleUpdateParamsPartnerSelected = (tab: any, partnerId: any) => {
    if (partnerId) {
      partnerQuestId = partnerId;
      window.history.replaceState(
        null,
        "",
        window.location.pathname + `?tab=${tab}&id=${partnerId}`,
      );
    } else {
      window.history.replaceState(
        null,
        "",
        window.location.pathname + `?tab=${tab}`,
      );
    }
  };

  const handleUpdatePartnerQuestsId = (id: any) => {
    partnerQuestId = id;
  };

  const handleClick = (e: any, tabValue: any) => {
    e.preventDefault();
    activeTabValue = tabValue;
    window.history.replaceState(
      null,
      "",
      window.location.pathname + `?tab=${tabValue}`,
    );
  };

  $: queryLinkSocial = createQuery({
    queryKey: ["link-socials"],
    queryFn: () => getLinkData(),
    retry: false,
  });

  $: {
    if (!$queryLinkSocial.isError && $queryLinkSocial.data !== undefined) {
      socialData = $queryLinkSocial?.data;
    }
  }

  const onSubmitInviteCode = async (e: any) => {
    isLoadingSubmitInviteCode = true;
    const formData = new FormData(e?.target);
    const data: any = {};
    for (let field of formData) {
      const [key, value] = field;
      data[key] = value;
    }
    try {
      const response: any = await nimbus.post(
        "/v2/campaign/sui-unlock/invitation",
        {
          code: data.code,
        },
      );
      if (response?.error) {
        triggerToast(
          response?.error ||
            "Something wrong when submit your invitation code. Please try again!",
          "fail",
        );
        return;
      }

      queryClient?.invalidateQueries([$userPublicAddress, "daily-checkin"]);

      triggerToast("Successfully submit your invitation code!", "success");

      code = "";
      localStorage.setItem("isSkipInviteCodeCampaign", "true");
      isSkipToMainPage = true;
    } catch (e) {
      console.error(e);
      triggerToast(
        "Something wrong when submit your invitation code. Please try again!",
        "fail",
      );
    } finally {
      isLoadingSubmitInviteCode = false;
    }
  };

  const handleSelectTabFlip = () => {
    activeTabValue = "flip";
    window.history.replaceState(
      null,
      "",
      window.location.pathname + "?tab=flip",
    );
  };
</script>

<ErrorBoundary>
  <div class="min-h-screen relative xl:z-9">
    {#if $isTWA}
      <div
        class="max-w-[1600px] m-auto w-[94%] py-8 grid xl:grid-cols-6 grid-cols-1 gap-6 relative xl:z-2"
      >
        <div class="col-span-1">
          <div class="w-full flex flex-col gap-4">
            {#each [{ label: "Daily Check in", value: "checkin", type: "Daily Checkin" }, { label: "Leaderboard", value: "leaderboard", type: "Leaderboard" }, { label: "Rewards", value: "rewards", type: "Rewards" }] as item}
              <div
                on:click={(e) => handleClick(e, item.value)}
                class={`flex items-center gap-2 rounded-[10px] py-2 px-3 cursor-pointer transition-all ${
                  $isDarkMode
                    ? activeTabValue === item.value
                      ? "text-blue-500 bg-[#ffffff1c]"
                      : "text-white hover:bg-[#222222]"
                    : activeTabValue === item.value
                      ? "text-blue-500 bg-gray-200"
                      : "text-gray-500 hover:bg-gray-100"
                }`}
              >
                <Icon
                  type={item?.type}
                  active={activeTabValue === item.value ? true : false}
                />
                <div class="xl:text-base text-lg font-medium">
                  {$t(item.label)}
                </div>
              </div>
            {/each}
          </div>
        </div>
        <div class="xl:col-span-5 col-span-1">
          {#if activeTabValue === "checkin"}
            <TabDailyCheckin {currentRoute} {activeTabValue} />
          {:else if activeTabValue === "leaderboard"}
            <TabLeaderBoard />
          {:else if activeTabValue === "rewards"}
            <TabReward {handleSelectTabFlip} />
          {:else if activeTabValue === "flip"}
            <TabFlipGmPoints />
          {/if}
        </div>
      </div>
    {/if}

    {#if !$isTWA}
      {#if socialData && socialData.find((item) => item?.type === "twitter")}
        {#if isSkipToMainPage || dataReferrals?.referrer}
          <div
            class="max-w-[1600px] m-auto w-[94%] py-8 grid xl:grid-cols-6 grid-cols-1 gap-6 relative xl:z-2"
          >
            <div class="col-span-1">
              <div class="w-full flex flex-col gap-4">
                {#each listSideBar || [] as item}
                  <div
                    on:click={(e) => handleClick(e, item.value)}
                    class={`flex items-center gap-2 rounded-[10px] py-2 px-3 cursor-pointer transition-all ${
                      $isDarkMode
                        ? activeTabValue === item.value
                          ? "text-blue-500 bg-[#ffffff1c]"
                          : "text-white hover:bg-[#222222]"
                        : activeTabValue === item.value
                          ? "text-blue-500 bg-gray-200"
                          : "text-gray-500 hover:bg-gray-100"
                    }`}
                  >
                    <Icon
                      type={item?.type}
                      active={activeTabValue === item.value ? true : false}
                    />
                    <div class="xl:text-base text-lg font-medium">
                      {$t(item.label)}
                    </div>
                  </div>
                {/each}
              </div>
            </div>
            <div class="xl:col-span-5 col-span-1">
              {#if activeTabValue === "quests"}
                <TabQuests
                  {socialData}
                  {partnerQuestId}
                  {handleUpdatePartnerQuestsId}
                />
              {:else if activeTabValue === "checkin"}
                <TabDailyCheckin {currentRoute} {activeTabValue} />
              {:else if activeTabValue === "leaderboard"}
                <TabLeaderBoard />
              {:else if activeTabValue === "rewards"}
                <TabReward {handleSelectTabFlip} />
              {:else if activeTabValue === "flip"}
                <TabFlipGmPoints />
              {/if}
            </div>
          </div>
        {:else}
          <div class="h-screen flex items-center justify-center relative z-9">
            <div class="flex flex-col items-center justify-center">
              <div
                class="text-3xl font-medium flex items-center justify-center flex-wrap gap-2"
              >
                Use an invite code for <span class="flex items-center gap-1">
                  <img src={Diamond} alt="" class="md:w-10 md:h-10 w-7 h-7" /> 1000
                  GM Points</span
                >
              </div>
              <div
                class="flex flex-col gap-3 justify-center items-center mt-4 w-full"
              >
                <form
                  on:submit|preventDefault={onSubmitInviteCode}
                  class="flex items-center gap-3 w-full"
                >
                  <div
                    class={`input-2 w-full input-border xl:py-[6px] py-3 px-3 ${
                      code && !$isDarkMode ? "bg-[#F0F2F7]" : "bg_fafafbff"
                    }`}
                  >
                    <input
                      type="text"
                      id="code"
                      name="code"
                      required
                      placeholder="Your Invite code"
                      bind:value={code}
                      class={`p-0 border-none focus:outline-none focus:ring-0 text-sm font-normal w-full ${
                        code && !$isDarkMode ? "bg-[#F0F2F7]" : "bg-transparent"
                      } ${
                        $isDarkMode
                          ? "text-white"
                          : "text-[#5E656B] placeholder-[#5E656B]"
                      }`}
                      on:change={(event) => {
                        code = event?.target?.value;
                      }}
                    />
                  </div>
                  <div class="w-[160px] xl:h-[40px] h-[50px]">
                    <Button
                      type="submit"
                      isLoading={isLoadingSubmitInviteCode}
                      disabled={isLoadingSubmitInviteCode}
                    >
                      <div class="uppercase">Let's roll</div>
                    </Button>
                  </div>
                </form>
                <div
                  class="text-gray-400 xl:text-sm text-base hover:underline transition-all cursor-pointer"
                  on:click={() => {
                    isSkipToMainPage = true;
                    localStorage.setItem("isSkipInviteCodeCampaign", "true");
                  }}
                >
                  Skip
                </div>
              </div>
            </div>
          </div>
        {/if}
      {:else}
        <div class="h-screen flex items-center justify-center relative xl:z-2">
          {#if $queryLinkSocial.isFetching}
            {#if isNimbusVersion}
              <LoadingPremium />
            {:else}
              <Loading />
            {/if}
          {:else}
            <ConnectX />
          {/if}
        </div>
      {/if}
    {/if}

    <div
      class="absolute z-1 xl:bottom-[-60px] bottom-[-40px] lg:left-[-120px] left-0 xl:block hidden"
    >
      <img src={wheelIcon} alt="" class="w-[70%] h-[70%] object-contain" />
    </div>
  </div>
</ErrorBoundary>

<style></style>
